import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import Presenter from './Presenter';
import './Popol.css';

const Container = styled.div`
    position:absolute;
    width:100%;
    color:white;
    text-align: center;
`;

const Header = styled.div`
    width:100%;
    height:100px;
    font-size: 26px;
    font-weight: 500;
    color:black;
    position:fixed;
    top:0px;
    z-index:999999;
    background-color: white;
`;

const Nav = styled.div`
    width:calc(25% - 32px);
    height: calc(100%);
    padding:35px;
    background-color: ${props => props.Bck ? "gray" : "none"};
    color:white;
    z-index:${props => props.index};
    transition:all 1s;
    position:relative;
    left:${props => props.Move};
    opacity: ${props => props.Page === 4 ? "0" : "1"};
`;

const Last = styled.div`
    width:100%;
    height: 100px;
    font-size:24px;
    font-weight:600;
    position:absolute;
    opacity:${props => props.Page === 4 ? "1" : "0"};
`;

const Lasttxt = styled.span`
    position:relative;
    top:35px;
    left:155px;
    z-index:99999999999;
`;

const Btn = styled.span`
    position:relative;
    top:${props => props.top};
    text-shadow: 
    1px 1px 1px black,
    2px 2px 1px gray;
    color:black;
    transition:0.3s;
`;

const Button = styled.div`
    width: 35px;
    height: 75px;
    border:3px solid black;
    ${props => props.radius ? "border-radius: 100%" : null};
    position: fixed;
    top:${props => props.top};
    right:170px;
    font-size: ${props => props.font};
    font-weight: ${props => props.type};
    box-shadow: 
    1px 1px 1px black,
    2px 2px 1px black,
    3px 3px 1px gray,
    4px 4px 1px gray;
    transition:0.5s ;
    &:hover{
        box-shadow: none;
        transform:translateY(5px);
        ${Btn}{
            color: #8ab4f8;
            text-shadow:none;
        }
    }
    z-index:99999;
    overflow:hidden;
`;

const BodyEvent = () => {

    const [Page, setPage] = useState(1);

    const handle = (signal, element) => {
        if (signal === true) {
            window.scrollTo({
                top: element == 2 ? window.innerHeight * 1 :
                    element == 3 ? window.innerHeight * 2 : window.innerHeight * 3,
                behavior: "smooth"
            });

        } else if (signal === false) {
            window.scrollTo({
                top: element == 3 ? window.innerHeight * 2 :
                    element == 2 ? window.innerHeight * 1 : 0,
                behavior: "smooth"
            });

        }

    }

    useEffect(() => {
        setPage(1);
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }, [])

    const top = (e) => {
        e.preventDefault();
        setPage(1);
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }

    let play = true;
    useEffect(() => {
        window.addEventListener("wheel",
            (e) => {
                e.preventDefault();
                let valueY = e.deltaY;

                if (play === false) { return };
                if (valueY === 100 && play === true) {
                    play = false;
                    const watch = () => {
                        setPage(Page + 1 >= 5 ? 4 : Page + 1)
                        window.addEventListener('scroll', handle(true, Page + 1 >= 5 ? 4 : Page + 1));
                    }
                    watch();
                    return () => {
                        window.removeEventListener('scroll', handle);
                        play = true;
                    }
                } else if (valueY === -100 && play === true) {
                    play = false;
                    const watch = () => {
                        setPage(Page - 1 <= 0 ? 1 : Page - 1)
                        window.addEventListener('scroll', handle(false, Page - 1 <= 0 ? 1 : Page - 1));
                    }
                    watch();
                    return () => {
                        window.removeEventListener('scroll', handle);
                        play = true;
                    }
                }
            },
            { passive: false }
        );
    }, [Page])

    return <>
        <Header>
            <div style={{ display: "flex", width: "100%", height: "100%", margin: "0px -13.3%" }}>
                <Nav index={1} Bck={true} Move={`calc(25 * ${Page}% - 32 * ${Page}px)`} Page={Page}></Nav>
                <Nav index={999} Bck={false}>I. 사용가능 프로그램</Nav>
                <Nav index={999} Bck={false}>II. 프로젝트1</Nav>
                <Nav index={999} Bck={false}>III. 프로젝트2</Nav>
                <Last Page={Page}>
                    <Lasttxt>감사합니다</Lasttxt>
                </Last>
            </div>
        </Header>
        <a onClick={top} href={"#"} style={Page >= 2 ? { display: "inline" } : { display: "none" }}>
            <Button
                top={"calc(70%)"}
                font={"20px"}
                type={"550"}
                radius={false}
            >
                <Btn top={"0px"} type={true}>
                    ▲<br />
                    ▲<br />
                    ▲<br />
                </Btn>
            </Button>
        </a>
    </>
}

const PopolContainer = () => {

    const Event = BodyEvent()

    return <Container key={Event.key}>
        {Event}
        <Presenter Delay={2} />
    </Container>
}
export default PopolContainer;
